import settings from './settings';
import initWidgetComponents from './widgetComponents';

const env = process.env.NODE_ENV;
// enviroment setup for sdk
const url = settings.enviroments[env];

function initAnalytics() {
    window.dataLayer = window.dataLayer || [];
    function gtag() {
        dataLayer.push(arguments);
    }
    gtag('js', new Date());

    gtag('config', 'UA-13057225-3', {
        linker: {
            domains: ['showpass.com'],
        },
    });

    const mutationObserver = new MutationObserver(function(mutations) {
        mutations.forEach(function(mutation) {
            if (mutation.target.className == 'showpass-widget-body') {
                var gobj = window[window.GoogleAnalyticsObject];
                var tracker, linker;
                let iFrame = document.getElementById('showpass-widget');
                if (gobj) {
                    tracker = gobj.getAll()[0];
                    linker = new window.gaplugins.Linker(tracker);
                    iFrame.src = linker.decorate(iFrame.src);
                }
            }
        });
    });

    mutationObserver.observe(document.documentElement, { attributes: true });
}

(function(window, document, src) {
    let config = window.__shwps;
    if (typeof config === 'undefined') {
        config = function() {
            config.c(arguments);
        };
        config.q = [];
        config.c = function(args) {
            config.q.push(args);
        };
        window.__shwps = config;

        const s = document.createElement('script');
        s.type = 'text/javascript';
        s.async = true;
        s.src = src;

        var x = document.getElementsByTagName('script')[0];
        x.parentNode.insertBefore(s, x);

        window.addEventListener('load', function() {
            initAnalytics();
            console.log('showpass: ', window.showpass);
            initWidgetComponents(window.showpass);
        });
    }
})(window, document, url);
