import widgets from './widgets';

function createWidget({ config, id, name, func }) {
    const btn = document.createElement('button');
    const classNames = ['btn', 'btn-secondary', 'btn-dark', 'btn-block'];
    btn.innerHTML = name;
    classNames.forEach((className) => btn.classList.add(className));
    btn.onclick = () => func(id, config);

    const wrapper = document.createElement('li');
    wrapper.className = 'list-group-item';
    wrapper.appendChild(btn);

    return wrapper;
}

const initWidgetComponents = (showpass) => {
    const container = document.getElementById('widget-components');

    const components = widgets(showpass);

    components.forEach((component) => {
        const widget = createWidget(component);
        container.appendChild(widget);
    });
};

export default initWidgetComponents;
